import Image from "../image"

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faEnvelope } from "@fortawesome/free-solid-svg-icons"
import {
  faTwitter,
  faGithub,
  faLinkedin,
} from "@fortawesome/free-brands-svg-icons"

export default function Hero() {
  return (
    <section className="hero" id="about">
      <div className="container h-100">
        <div className="row h-100 justify-content-around">
          <div className="col-lg-5 my-auto">
            <div className="hero-introduction">
              <h1>
                <span className="wave"></span>
              </h1>
              <h1>Hi. Hola. Bonjour.</h1>
              <p>
                Nice to meet you! I am Goldy Mariz, and I write code for a
                living. Currently open for interesting collaborations!
              </p>
              <ul className="list-group list-group-horizontal">
                <li className="list-group-item">
                  <a href="mailto:gmlunesa@gmail.com">
                    <FontAwesomeIcon icon={faEnvelope} />
                  </a>
                </li>
                <li className="list-group-item">
                  <a href="https://github.com/gmlunesa">
                    <FontAwesomeIcon icon={faGithub} />
                  </a>
                </li>
                <li className="list-group-item">
                  <a href="https://linkedin.com/gmlunesa">
                    <FontAwesomeIcon icon={faLinkedin} />
                  </a>
                </li>
                <li className="list-group-item">
                  <a href="https://twitter.com/gmlunesa">
                    <FontAwesomeIcon icon={faTwitter} />
                  </a>
                </li>
              </ul>
            </div>
          </div>
          <div className="col-lg-4 my-auto">
            <div className="hero-bio">
              <div className="card shadow-sm mb-5">
                <div className="card-body">
                  <div className="icon">
                    <Image />
                  </div>
                  <h5 className="card-title">@gmlunesa</h5>
                  <h6>
                     Cebu City <br />
                  </h6>
                  <p className="card-text">
                    software engineer. imaginary astronaut.
                  </p>
                  <a
                    href="https://twitter.com/gmlunesa"
                    className="follow-button btn btn-primary shadow-lg"
                    target="_blank"
                  >
                    Follow
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}
